import React, { Component } from 'react';
import Navigation from './Navigation'
import ios_app from './ios_app.png'
import {Card} from 'react-bootstrap'

class Main extends Component {


    render () {
        return (
           <div>
               <Navigation />
            <div>
                <img src={ios_app} className="Ios-logo" alt="logo" />
                <h1>
                    The Breaktime Digest App
                </h1>
                <p>
                    Catch up on the latest news stories and trending topics
                </p>
            </div>
            <hr></hr>
            <div>
                <b><h1>
                    About
                </h1></b>
                <h4>This app was an idea after spending hours searching through news stories and apps for the content I cared about. </h4>
                <h4>It pulls the lates news stories or trending articles for you to visit. It also shows the top reddit comments for that article as well.</h4>
                <h5>I'm a sole, full-stack developer looking for collaboration projects and opportunities!</h5>
                <a name="about"
                className="App-link"
                href="https://reactjs.org"
                target="_blank"
                rel="noopener noreferrer"
                ></a>
            </div>
            <hr></hr>
            <div>
                <h1>
                    Contact
                </h1>
                <h4>Have any questions, comments, issues?</h4>
                <h4>Shoot me an email: <a name="contact"
                href="mailto:summrize@gmail.com"
                target="_top"
                >summrize</a></h4>
            </div>
            <hr></hr>
            <div className="container">
            <p>
                <Card>
                    <Card.Header>Extras for Programmers</Card.Header>
                    <Card.Body>
                        <Card.Title>What's behind the App?</Card.Title>
                        <hr></hr>
                        <Card.Text>
                            Ubuntu Sever - Ngnix - MongoDB
                        </Card.Text>
                        <hr></hr>
                        <Card.Text>
                            ASP.NET Core 1.2 API - .NET Core Console App
                        </Card.Text>
                        <hr></hr>
                        <Card.Text>
                            React Native - (using) Expo.io
                        </Card.Text>
                        <hr></hr>
                        <Card.Text>
                            Dope course on <a href="https://frontendmasters.com/courses/full-stack-v2/">Frontend Stuff</a>
                        </Card.Text>
                    </Card.Body>
                </Card></p>
            </div>
            <hr></hr>
        </div> 
        )
    }
}

export default Main;